"use client"

import { useState, useEffect, useCallback } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import Image from "next/image"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { 
  Dialog, DialogContent, DialogHeader, DialogTitle 
} from "@/components/ui/dialog"
import { 
  Plus, Play, Square, Slack, LogOut, Activity, 
  Sparkles, UserCheck, Eye, ShieldCheck, Zap, Info, Send, ChevronLeft, ChevronRight, Loader2,
  Trophy
} from "lucide-react"
import logo from "@/lib/images/image.png"

// --- 인터페이스 정의 ---
interface Promotion {
  id: string; 
  name: string;         
  pointAmount: number; 
  totalStock: number;  
  startTime: string;   
  endTime: string;     
  status: "SCHEDULED" | "ACTIVE" | "ENDED";
  etc: string | null;
  createdAt: string; 
}

interface PromotionStatus {
  queueSize: number;
  participantCount: number;
  winnerCount: number;
  totalStock: number;
  remainingStock: number;
}

interface WinnerListResponse {
  userId: string;
  queuePosition: number;
  participatedAt: string;
}

interface AdvisorRequest {
  userId: string;
  userName: string;
  name: string;
  email: string;
  appliedAt: string;
  reason: string;
}

export default function AdminDashboard() {
  const router = useRouter()
  const [isMounted, setIsMounted] = useState(false)
  const [isLoading, setIsLoading] = useState(false)
  const [userRole, setUserRole] = useState<string | null>(null)
  const [userName, setUserName] = useState("")

  // --- API 경로 ---
  const BASE_URL = "http://34.50.7.8:3000"
  const PROMO_API = `${BASE_URL}/v1/promotions`
  const ADMIN_USER_API = `${BASE_URL}/v1/user` 

  const [promotions, setPromotions] = useState<Promotion[]>([])
  const [advisorRequests, setAdvisorRequests] = useState<AdvisorRequest[]>([])
  const [selectedRequest, setSelectedRequest] = useState<AdvisorRequest | null>(null)
  const [isDetailOpen, setIsDetailOpen] = useState(false)
  const [isPromoCreateOpen, setIsPromoCreateOpen] = useState(false)

  // --- 실시간 기능 상태 ---
  const [winners, setWinners] = useState<WinnerListResponse[]>([])
  const [isWinnersOpen, setIsWinnersOpen] = useState(false)
  const [statusData, setStatusData] = useState<PromotionStatus | null>(null)
  const [isStatusOpen, setIsStatusOpen] = useState(false)
  const [targetPromoId, setTargetPromoId] = useState<string | null>(null) 
  const [targetPromoName, setTargetPromoName] = useState("")

  const [promoPage, setPromoPage] = useState(0)
  const [totalPromoPages, setTotalPromoPages] = useState(1)
  const itemsPerPage = 5 

  const refreshStatus = useCallback(async (id: string) => {
    const token = localStorage.getItem("accessToken")
    try {
      const res = await fetch(`${PROMO_API}/${id}/status`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) setStatusData(data.result)
    } catch (e) { console.error("현황 갱신 오류") }
  }, [PROMO_API])

  useEffect(() => {
    let timer: NodeJS.Timeout
    if (isStatusOpen && targetPromoId) {
      timer = setInterval(() => refreshStatus(targetPromoId), 3000)
    }
    return () => clearInterval(timer)
  }, [isStatusOpen, targetPromoId, refreshStatus])

  const fetchWinners = async (id: string, name: string) => {
    const token = localStorage.getItem("accessToken")
    try {
      setTargetPromoName(name)
      const res = await fetch(`${PROMO_API}/${id}/winners`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) {
        setWinners(data.result)
        setIsWinnersOpen(true)
      }
    } catch (error) { alert("당첨자 목록 로드 실패"); }
  }

  const openStatusMonitor = (id: string, name: string) => {
    setTargetPromoId(id)
    setTargetPromoName(name)
    refreshStatus(id) 
    setIsStatusOpen(true)
  }

  const fetchPromotions = useCallback(async () => {
    const token = localStorage.getItem("accessToken")
    try {
      setIsLoading(true)
      const res = await fetch(`${PROMO_API}?page=${promoPage}&size=${itemsPerPage}`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) {
        setPromotions(data.result.content); 
        setTotalPromoPages(data.result.totalPages);
      }
    } catch (error) { console.error("로드 실패", error); } finally { setIsLoading(false); }
  }, [promoPage, PROMO_API])

  const fetchPendingAdvisors = useCallback(async () => {
    const token = localStorage.getItem("accessToken")
    try {
      const res = await fetch(`${ADMIN_USER_API}/advisor/pending`, {
        method: "GET",
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) { setAdvisorRequests(data.result); }
    } catch (error) { console.error("심사 로드 실패", error); }
  }, [ADMIN_USER_API])

  useEffect(() => {
    const role = localStorage.getItem("role")
    const name = localStorage.getItem("name") || "관리자"
    if (role !== "MANAGER" && role !== "MASTER") { router.push("/login"); return; }
    setUserRole(role); setUserName(name); setIsMounted(true);
    fetchPromotions(); fetchPendingAdvisors();
  }, [router, fetchPromotions, fetchPendingAdvisors])

  const [newPromo, setNewPromo] = useState({ 
    name: "", pointAmount: "", totalStock: "", 
    startTime: "", endTime: "" 
  })

  const handleCreatePromotion = async () => {
    const token = localStorage.getItem("accessToken")
    if (!newPromo.name || !newPromo.pointAmount || !newPromo.totalStock) return alert("필수 정보를 입력하세요.")
    try {
      const res = await fetch(PROMO_API, {
        method: "POST",
        headers: { "Authorization": `Bearer ${token}`, "Content-Type": "application/json" },
        body: JSON.stringify({ 
          ...newPromo, 
          totalStock: Number(newPromo.totalStock), 
          pointAmount: Number(newPromo.pointAmount),
          createdAt: new Date().toISOString()
        })
      })
      const data = await res.json()
      if (data.isSuccess) { alert("프로모션이 생성되었습니다."); setIsPromoCreateOpen(false); fetchPromotions(); }
    } catch (error) { alert("생성 중 오류 발생"); }
  }

  const handleTogglePromotion = async (id: string, currentStatus: string) => {
    const token = localStorage.getItem("accessToken")
    const action = currentStatus === "SCHEDULED" ? "start" : "end"
    if (!confirm(`프로모션을 ${action === "start" ? "시작" : "종료"}하시겠습니까?`)) return
    try {
      const res = await fetch(`${PROMO_API}/${id}/${action}`, {
        method: "PATCH",
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) fetchPromotions()
    } catch (error) { alert("상태 변경 실패"); }
  }

  const handleProcessAdvisor = async (userId: string, isApprove: boolean) => {
    const token = localStorage.getItem("accessToken")
    const actionText = isApprove ? "승인" : "거절"
    if (!isApprove && !confirm("해당 신청을 거절하시겠습니까?")) return
    try {
      const res = await fetch(`${ADMIN_USER_API}/advisor/${userId}/approve`, {
        method: "POST", headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) { alert(data.message || `${actionText} 처리되었습니다.`); setIsDetailOpen(false); fetchPendingAdvisors(); }
    } catch (error) { alert("서버 통신 오류"); }
  }

  const handleSlackTest = async () => {
    const token = localStorage.getItem("accessToken");
    try {
      const res = await fetch(`${PROMO_API}/test/monitoring/test-slack-only`, {
        method: "POST", headers: { "Authorization": `Bearer ${token}` }
      });
      const data = await res.json();
      if (data.isSuccess) alert("슬랙 테스트 메시지 전송 성공");
    } catch (e) { alert("슬랙 API 통신 오류"); }
  };

  const handleSendSlackReport = async () => {
    const token = localStorage.getItem("accessToken");
    if (!confirm("현황 보고서를 슬랙으로 전송하시겠습니까?")) return;
    try {
      const res = await fetch(`${PROMO_API}/monitoring/report/manual`, {
        method: "POST", headers: { "Authorization": `Bearer ${token}` }
      });
      const data = await res.json();
      if (data.isSuccess) alert("슬랙 보고서가 전송되었습니다.");
    } catch (e) { console.log("보고서 API 통신 오류"); }
  };

  if (!isMounted) return null

  return (
    <div className="min-h-screen bg-background text-foreground font-sans">
      <header className="border-b border-border bg-card/50 backdrop-blur-sm sticky top-0 z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <Link href="/admin" className="flex items-center gap-2">
            <Image src={logo} alt="Next Me" width={100} height={28} className="h-7 w-auto" priority />
            <Badge variant="outline" className="ml-2 border-primary text-primary font-bold uppercase text-[10px]">{userRole} ADMIN</Badge>
          </Link>
          <div className="flex items-center gap-4">
            <Avatar className="h-9 w-9 border-2 border-primary/20">
              <AvatarFallback className="bg-primary text-primary-foreground text-xs font-black uppercase">{userName?.charAt(0)}</AvatarFallback>
            </Avatar>
            <Button variant="ghost" size="icon" onClick={() => {localStorage.clear(); router.push("/login");}} className="rounded-xl"><LogOut className="h-5 w-5" /></Button>
          </div>
        </div>
      </header>

      <main className="container mx-auto px-4 py-8">
        <div className="flex flex-col md:flex-row justify-between items-start md:items-end gap-4 mb-8">
          <h1 className="text-2xl md:text-3xl font-black tracking-tighter uppercase italic">환영합니다, {userName} 관리자님.</h1>
          <div className="flex gap-2 w-full md:w-auto">
            <Button variant="outline" size="sm" className="flex-1 md:flex-none rounded-xl h-10" onClick={handleSlackTest}><Slack className="h-4 w-4 mr-2" /> Slack 테스트</Button>
            <Button size="sm" className="flex-1 md:flex-none bg-primary rounded-xl h-10" onClick={handleSendSlackReport}><Send className="h-4 w-4 mr-2" /> 슬랙 보고 전송</Button>
          </div>
        </div>

        <div className="grid grid-cols-1 sm:grid-cols-3 gap-6 mb-8">
          <Card className="p-6 border-border shadow-none rounded-2xl"><Activity className="h-5 w-5 text-primary mb-4" /><p className="text-2xl font-black">Operational</p></Card>
          <Card className="p-6 border-border shadow-none rounded-2xl"><Zap className="h-5 w-5 text-accent mb-4" /><p className="text-2xl font-black">{promotions.filter(p=>p.status==="ACTIVE").length} ACTIVE</p></Card>
          <Card className="p-6 border-border shadow-none rounded-2xl"><UserCheck className="h-5 w-5 text-orange-500 mb-4" /><p className="text-2xl font-black text-orange-500">{advisorRequests.length} PENDING</p></Card>
        </div>

        <div className="grid lg:grid-cols-3 gap-8">
          <div className="lg:col-span-2">
            <Tabs defaultValue="promotions" className="w-full">
              <TabsList className="bg-secondary/30 mb-6 p-1 rounded-xl w-fit">
                <TabsTrigger value="promotions" className="font-bold rounded-lg px-6 uppercase italic">Promotions</TabsTrigger>
                <TabsTrigger value="advisors" className="font-bold rounded-lg px-6 uppercase italic">Advisors</TabsTrigger>
              </TabsList>

              <TabsContent value="promotions" className="mt-0">
                <Card className="p-4 sm:p-6 border-border shadow-none flex flex-col min-h-[500px] rounded-2xl bg-card/50">
                  <div className="flex items-center justify-between mb-6">
                    <h2 className="text-xl font-black italic uppercase flex items-center gap-2"><Sparkles className="h-5 w-5 text-primary" /> 이벤트 현황</h2>
                    <Button size="sm" onClick={() => setIsPromoCreateOpen(true)} className="rounded-xl font-bold h-9"><Plus className="h-4 w-4 mr-1" /> 신규 등록</Button>
                  </div>
                  
                  <div className="overflow-x-auto">
                    {/* 너비 비율 및 최소 너비 최적화 */}
                    <table className="w-full text-left font-sans table-fixed min-w-[650px]">
                      <thead className="text-muted-foreground text-[10px] font-black uppercase border-b border-border/50">
                        <tr>
                          <th className="px-2 py-3 w-[25%]">Campaign</th>
                          <th className="px-2 py-3 w-[30%]">Schedule</th>
                          <th className="px-2 py-3 text-center w-[15%]">Status</th>
                          <th className="px-2 py-3 text-right w-[30%]">Control</th>
                        </tr>
                      </thead>
                      <tbody className="divide-y divide-border/50 text-sm font-medium">
                        {promotions.map((p) => (
                          <tr key={p.id} className="group hover:bg-secondary/10 transition-all">
                            <td className="px-2 py-5 overflow-hidden">
                              <p className="font-bold text-foreground italic uppercase truncate">{p.name}</p>
                              <p className="text-[10px] text-primary font-mono font-bold mt-1 tracking-tighter">{p.pointAmount.toLocaleString()}P · {p.totalStock}명</p>
                            </td>
                            <td className="px-2 py-5 font-mono text-[10px] space-y-1">
                              <div className="flex items-center gap-1 truncate"><Badge variant="outline" className="h-3.5 px-1 text-[7px] border-green-500/50 text-green-600">S</Badge> {p.startTime.slice(5, 16).replace('T', ' ')}</div>
                              <div className="flex items-center gap-1 truncate"><Badge variant="outline" className="h-3.5 px-1 text-[7px] border-destructive/50 text-destructive">E</Badge> {p.endTime.slice(5, 16).replace('T', ' ')}</div>
                            </td>
                            <td className="px-1 py-5 text-center">
                              <Badge variant={p.status === "ACTIVE" ? "default" : "secondary"} className="rounded-md font-black uppercase text-[8px] h-5 justify-center px-1.5">{p.status}</Badge>
                            </td>
                            <td className="px-2 py-5">
                              {/* 버튼 겹침 및 압축 방지 설정 */}
                              <div className="flex items-center justify-end gap-1.5 flex-nowrap">
                                {p.status === "SCHEDULED" && (
                                  <Button size="icon" variant="ghost" className="h-8 w-8 text-green-600 hover:bg-green-50 shrink-0" onClick={() => handleTogglePromotion(p.id, "SCHEDULED")}>
                                    <Play className="h-3.5 w-3.5 fill-current" />
                                  </Button>
                                )}
                                {p.status === "ACTIVE" && (
                                  <Button size="icon" variant="ghost" className="h-8 w-8 text-destructive hover:bg-destructive/5 shrink-0" onClick={() => handleTogglePromotion(p.id, "ACTIVE")}>
                                    <Square className="h-3.5 w-3.5 fill-current" />
                                  </Button>
                                )}
                                <Button size="icon" variant="ghost" className="h-8 w-8 text-orange-500 hover:bg-orange-50 shrink-0" onClick={() => fetchWinners(p.id, p.name)}>
                                  <Trophy className="h-3.5 w-3.5" />
                                </Button>
                                <Button size="icon" variant="ghost" className="h-8 w-8 text-primary hover:bg-primary/5 shrink-0" onClick={() => openStatusMonitor(p.id, p.name)}>
                                  <Info className="h-3.5 w-3.5" />
                                </Button>
                              </div>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                  <div className="flex items-center justify-center gap-2 mt-6 pt-4 border-t border-border/50">
                    <Button variant="outline" size="icon" className="h-8 w-8 rounded-lg" onClick={() => setPromoPage(p => Math.max(0, p-1))} disabled={promoPage === 0}><ChevronLeft className="h-4 w-4" /></Button>
                    <span className="text-[10px] font-black px-4 italic">{promoPage + 1} / {totalPromoPages}</span>
                    <Button variant="outline" size="icon" className="h-8 w-8 rounded-lg" onClick={() => setPromoPage(p => Math.min(totalPromoPages - 1, p+1))} disabled={promoPage >= totalPromoPages - 1}><ChevronRight className="h-4 w-4" /></Button>
                  </div>
                </Card>
              </TabsContent>

              <TabsContent value="advisors" className="mt-0">
                <Card className="p-6 border-border shadow-none flex flex-col min-h-[500px] rounded-2xl bg-card/50">
                  <h2 className="text-xl font-bold mb-6 flex items-center gap-2 font-black italic uppercase"><UserCheck className="h-5 w-5 text-primary" /> 신청 심사</h2>
                  <div className="flex-1 space-y-3">
                    {advisorRequests.length > 0 ? advisorRequests.map((req) => (
                      <div key={req.userId} className="flex items-center justify-between p-4 rounded-2xl bg-secondary/10 border border-transparent hover:border-primary/20 hover:bg-white transition-all">
                        <div className="flex items-center gap-4">
                          <Avatar className="h-10 w-10 border-2 border-background"><AvatarFallback className="font-bold bg-secondary">{req.name?.[0]}</AvatarFallback></Avatar>
                          <div className="text-left"><p className="font-bold text-sm tracking-tight">{req.name}</p><p className="text-[10px] text-muted-foreground font-mono uppercase">{req.appliedAt?.split('T')[0]}</p></div>
                        </div>
                        <Button variant="outline" size="sm" className="h-8 font-bold rounded-xl px-4 border-primary/20" onClick={() => { setSelectedRequest(req); setIsDetailOpen(true); }}><Eye className="h-3.5 w-3.5 mr-2" /> 심사</Button>
                      </div>
                    )) : (
                      <div className="flex flex-col items-center justify-center py-20 opacity-30 font-black italic uppercase text-xs tracking-widest">No pending requests</div>
                    )}
                  </div>
                </Card>
              </TabsContent>
            </Tabs>
          </div>

          <div> 
            <Card className="p-6 border-border shadow-none bg-secondary/5 rounded-2xl border-dashed">
              <h2 className="text-lg font-black italic uppercase mb-6 flex items-center gap-2"><ShieldCheck className="h-5 w-5 text-primary" /> 가이드</h2>
              <div className="space-y-4">
                <div className="p-4 bg-background rounded-xl border border-border/50">
                  <p className="font-bold text-orange-500 text-[10px] uppercase mb-1 flex items-center gap-1"><Trophy className="h-3 w-3" /> Winner List</p>
                  <p className="text-[11px] font-bold italic leading-relaxed text-muted-foreground">트로피 아이콘을 클릭하여 실시간 당첨자 목록과 참여 순번을 확인하세요.</p>
                </div>
                <div className="p-4 bg-background rounded-xl border border-border/50">
                  <p className="font-bold text-primary text-[10px] uppercase mb-1 flex items-center gap-1"><Activity className="h-3 w-3" /> Live Monitor</p>
                  <p className="text-[11px] font-bold italic leading-relaxed text-muted-foreground">Info 아이콘을 통해 현재 대기열 크기와 잔여 수량을 실시간으로 모니터링할 수 있습니다.</p>
                </div>
              </div>
            </Card>
          </div>
        </div>
      </main>

      {/* --- 당첨자 목록 모달 --- */}
      <Dialog open={isWinnersOpen} onOpenChange={setIsWinnersOpen}>
        <DialogContent className="w-[92vw] sm:max-w-[420px] rounded-[1.5rem] p-6 border-none shadow-2xl">
          <DialogHeader><DialogTitle className="font-black italic uppercase text-lg flex items-center gap-2 text-orange-500"><Trophy className="h-5 w-5" /> {targetPromoName} Winners</DialogTitle></DialogHeader>
          <div className="max-h-[400px] overflow-y-auto pr-1 space-y-2 my-4">
            {winners.length > 0 ? winners.map((w, index) => (
              <div key={w.userId + index} className="flex items-center justify-between p-3 rounded-xl bg-secondary/10 border border-border/50">
                <div className="flex items-center gap-3">
                    <span className="text-xs font-black italic text-orange-600 w-5">#{index + 1}</span>
                    <div className="overflow-hidden">
                        <p className="text-[11px] font-mono font-bold text-foreground truncate w-[160px] sm:w-[200px]">{w.userId}</p>
                        <p className="text-[9px] font-mono text-muted-foreground">{w.participatedAt.replace('T', ' ').slice(5, 19)}</p>
                    </div>
                </div>
                <Badge variant="outline" className="text-[8px] font-black border-orange-500/20 text-orange-600 bg-orange-50 uppercase shrink-0 px-1.5 h-5">Win</Badge>
              </div>
            )) : <div className="py-14 text-center text-[10px] font-black uppercase opacity-40 italic tracking-widest">당첨자 데이터가 없습니다.</div>}
          </div>
          <Button onClick={() => setIsWinnersOpen(false)} className="w-full h-12 rounded-xl font-black uppercase italic tracking-widest">Close List</Button>
        </DialogContent>
      </Dialog>

      {/* --- 실시간 현황 모달 --- */}
      <Dialog open={isStatusOpen} onOpenChange={setIsStatusOpen}>
        <DialogContent className="w-[92vw] sm:max-w-[450px] rounded-[1.5rem] sm:rounded-[2rem] p-6 sm:p-8 border-none">
          <DialogHeader>
            <DialogTitle className="font-black italic text-lg sm:text-xl uppercase flex items-center justify-between">
              <span className="flex items-center gap-2"><Activity className="h-6 w-6 text-primary animate-pulse" /> Live Monitor</span>
              <Badge variant="outline" className="text-[10px] border-primary text-primary animate-pulse">실시간 갱신 중</Badge>
            </DialogTitle>
          </DialogHeader>
          {statusData ? (
            <div className="grid grid-cols-2 gap-3 py-4">
              <div className="p-3 sm:p-4 bg-secondary/20 rounded-xl border border-border/50"><p className="text-[9px] font-black uppercase text-muted-foreground">Queue Size</p><p className="text-lg sm:text-xl font-black">{statusData.queueSize.toLocaleString()}</p></div>
              <div className="p-3 sm:p-4 bg-secondary/20 rounded-xl border border-border/50"><p className="text-[9px] font-black uppercase text-muted-foreground">Participants</p><p className="text-lg sm:text-xl font-black">{statusData.participantCount.toLocaleString()}</p></div>
              <div className="p-3 sm:p-4 bg-primary/10 rounded-xl border border-primary/20 text-primary"><p className="text-[9px] font-black uppercase">Winners</p><p className="text-lg sm:text-xl font-black">{statusData.winnerCount.toLocaleString()}</p></div>
              <div className="p-3 sm:p-4 bg-secondary/20 rounded-xl border border-border/50"><p className="text-[9px] font-black uppercase text-muted-foreground">Total Stock</p><p className="text-lg sm:text-xl font-black">{statusData.totalStock.toLocaleString()}</p></div>
              <div className="col-span-2 p-5 bg-orange-500/10 rounded-xl border border-orange-500/20 text-center">
                <p className="text-[10px] font-black uppercase text-orange-600">Remaining Stock</p>
                <p className="text-4xl font-black text-orange-600 tracking-tighter">{statusData.remainingStock.toLocaleString()}</p>
              </div>
            </div>
          ) : (
            <div className="py-20 flex justify-center"><Loader2 className="animate-spin text-primary" /></div>
          )}
          <Button className="w-full h-12 font-black rounded-xl uppercase italic tracking-widest shadow-lg shadow-primary/20" onClick={() => setIsStatusOpen(false)}>Close Monitor</Button>
        </DialogContent>
      </Dialog>

      {/* --- 전문가 심사 모달 --- */}
      <Dialog open={isDetailOpen} onOpenChange={setIsDetailOpen}>
        <DialogContent className="w-[92vw] sm:max-w-[380px] rounded-[1.5rem] p-6 border-none shadow-2xl">
          <DialogHeader><DialogTitle className="font-black italic uppercase text-lg flex items-center gap-2"><UserCheck className="h-5 w-5 text-primary" /> 전문가 신청 심사</DialogTitle></DialogHeader>
          {selectedRequest && (
            <div className="space-y-4 py-2">
              <div className="flex items-center gap-3 p-3 rounded-xl bg-secondary/20 border border-border/50 shadow-inner">
                <Avatar className="h-12 w-12 border-2 border-white"><AvatarFallback className="font-black bg-primary text-white">{selectedRequest.name?.[0]}</AvatarFallback></Avatar>
                <div className="overflow-hidden">
                  <p className="font-black text-sm uppercase truncate">{selectedRequest.name}</p>
                  <p className="text-[10px] text-muted-foreground font-mono truncate">{selectedRequest.email}</p>
                </div>
              </div>
              <div className="p-4 bg-secondary/5 border border-border/40 rounded-xl text-[11px] font-bold italic leading-relaxed text-muted-foreground">"{selectedRequest.reason}"</div>
              <div className="grid grid-cols-2 gap-3 pt-2">
                <Button variant="outline" className="font-black uppercase italic text-[11px] h-12 rounded-xl border-2 hover:bg-destructive/10 hover:text-destructive hover:border-destructive transition-all" onClick={() => handleProcessAdvisor(selectedRequest.userId, false)}>Reject</Button>
                <Button className="font-black uppercase italic text-[11px] h-12 rounded-xl shadow-lg shadow-primary/20" onClick={() => handleProcessAdvisor(selectedRequest.userId, true)}>Approve Access</Button>
              </div>
            </div>
          )}
        </DialogContent>
      </Dialog>

      {/* --- 캠페인 신규 등록 모달 --- */}
      <Dialog open={isPromoCreateOpen} onOpenChange={setIsPromoCreateOpen}>
        <DialogContent className="w-[92vw] sm:max-w-[420px] rounded-[2rem] p-8 border-none shadow-2xl">
          <DialogHeader><DialogTitle className="font-black italic uppercase text-xl">New Campaign</DialogTitle></DialogHeader>
          <div className="grid gap-4 py-2">
            <div className="space-y-1"><Label className="text-[10px] font-black uppercase text-muted-foreground">Campaign Name</Label><Input value={newPromo.name} onChange={(e) => setNewPromo({...newPromo, name: e.target.value})} className="bg-secondary/20 border-none h-11 rounded-xl font-bold" /></div>
            <div className="grid grid-cols-2 gap-3">
              <div className="space-y-1"><Label className="text-[10px] font-black uppercase text-muted-foreground">Points</Label><Input type="number" value={newPromo.pointAmount} onChange={(e) => setNewPromo({...newPromo, pointAmount: e.target.value})} className="bg-secondary/20 border-none h-11 rounded-xl font-mono" /></div>
              <div className="space-y-1"><Label className="text-[10px] font-black uppercase text-muted-foreground">Stock</Label><Input type="number" value={newPromo.totalStock} onChange={(e) => setNewPromo({...newPromo, totalStock: e.target.value})} className="bg-secondary/20 border-none h-11 rounded-xl font-mono" /></div>
            </div>
            <div className="grid grid-cols-1 sm:grid-cols-2 gap-3">
              <div className="space-y-1"><Label className="text-[10px] font-black uppercase text-muted-foreground">Start Time</Label><Input type="datetime-local" value={newPromo.startTime.slice(0,16)} onChange={(e) => setNewPromo({...newPromo, startTime: e.target.value + ":00"})} className="bg-secondary/20 border-none h-11 rounded-xl text-[11px] font-mono" /></div>
              <div className="space-y-1"><Label className="text-[10px] font-black uppercase text-muted-foreground">End Time</Label><Input type="datetime-local" value={newPromo.endTime.slice(0,16)} onChange={(e) => setNewPromo({...newPromo, endTime: e.target.value + ":00"})} className="bg-secondary/20 border-none h-11 rounded-xl text-[11px] font-mono" /></div>
            </div>
          </div>
          <Button onClick={handleCreatePromotion} className="w-full h-14 font-black rounded-2xl uppercase italic tracking-widest shadow-xl shadow-primary/20 mt-4 transition-transform hover:scale-[1.02] active:scale-[0.98]">Deploy Campaign</Button>
        </DialogContent>
      </Dialog>
    </div>
  )
}